import React, {useState, useEffect} from 'react'
import {useForm} from 'react-hook-form'
import {withRouter} from 'react-router-dom'
import {accesoUsuarioAccion} from '../redux/usuarioDucks'
import {useDispatch, useSelector} from 'react-redux'

const Login = (props) => {

    const [login, setLogin] = useState(false)
    const {register, handleSubmit, reset, formState} = useForm({mode: 'onChange', reValidateMode:'onChange'})
    const dispatch = useDispatch()
    const activo = useSelector(store=> store.usuario.activo)

    const onSubmit = data => {
        if(login){
            dispatch(accesoUsuarioAccion('login_form', data))
        }
        else{
            dispatch(accesoUsuarioAccion('registro_form', data))
        }
        reset()
    }

    const logRRSS = data => {
        dispatch(accesoUsuarioAccion(data))
    }

    useEffect(()=>{
        if(activo===true){
            props.history.push('/inicio')
        }
        else{}
    },[props.history, activo])

    return (
        <div className="login d-md-flex align-items-center min-vh-100">
            <div className="row w-100 mx-auto justify-content-center">
                <div className={`login__panel col-12 col-md-10 rounded px-0 shadow-sm ${ login ? 'login' : 'registro'}`}>
                    <div className="login__texto py-4">
                        <div className="login__texto--contenido">
                            <h2 className="mt-4 mt-md-0 m fs-2 fw-bolder text-center text-white">
                                { login ? '¿Primera vez?' : '¿Ya tienes una cuenta?'}
                            </h2>
                            <p className="mt-4 mx-auto text-center w-75 fs-6 text-white fw-light">
                                { login? 'Regístrate para comenzar a usar esta app.' : 'Si ya tienes una cuenta, inicia sesión.'}
                            </p>
                            <button 
                                className="d-block btn btn-outline-light rounded-pill my-4 mx-auto px-3 fs-6 fw-bold text-white"
                                type="button"
                                onClick={()=>setLogin(!login)}
                            >
                                { login ? 'Registrarse' : 'Iniciar sesión'}
                            </button>
                        </div>                 
                    </div>
                    <div className="pt-4 login__formulario text-center">
                        <h1 className="my-4 fs-1 fw-bold text-success text-center">
                            { login ? 'Inicia sesión' : 'Regístrate aquí'}
                        </h1>
                        <button className="btn btn-rrss btn-rrss--google me-3" type="button" onClick={()=>logRRSS('google')}><i className="fab fa-google-plus-g fs-5"></i></button>
                        <button className="btn btn-rrss btn-rrss--github me-3" type="button" onClick={()=>dispatch(accesoUsuarioAccion('github'))}><i className="fab fa-github-alt fs-4"></i></button> 
                        <button className="btn btn-rrss btn-rrss--github" type="button" onClick={()=>dispatch(accesoUsuarioAccion('facebook', ''))}><i className="fab fa-facebook-f fs-5"></i></button> 
                        <form className="mt-4 mx-auto w-75" onSubmit={handleSubmit(onSubmit)}>
                            {
                                !login ? 
                                <>
                                    <p className="fs-6 fw-lighter text-secondary text-center">O regístrate completando tus datos.</p>
                                    <input 
                                        type="text"
                                        name="usuario"
                                        className="form-control py-2 rounded-pill bg-info border-0 w-75 mx-auto"
                                        placeholder="Nombre de usuario"
                                        ref={register({required: true})}
                                    />
                                </>
                                : 
                                <>
                                    <p className="fs-6 fw-lighter text-secondary text-center">O inicia sesión con tu email y contraseña.</p>
                                </>
                            }
                            
                            <input 
                                type="email"
                                name="email"
                                className="form-control mt-3 py-2 rounded-pill bg-info border-0 w-75 mx-auto"
                                placeholder="usuario@email.com"
                                ref={register({required:true})}
                            />
                            <input 
                                type="password"
                                name="password"
                                className="form-control mt-3 py-2 rounded-pill bg-info border-0 w-75 mx-auto"
                                placeholder={login? 'Contraseña' : 'Contraseña (min. 6 caracteres)'}
                                ref={register({required:true, minLength:6})}
                            />
                            <button 
                                className="btn btn-primary w-75 rounded-pill py-2 mt-3 mx-auto" 
                                type="submit"
                                disabled={!formState.isValid}
                            >
                                {login? 'Ingresar' : 'Registrarse'}
                            </button>
                            { login ? <p className="mt-3 fs-6 fw-lighter text-secondary text-center">¿Olvidaste tu contraseña?</p> : null}
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Login)
