import {auth, db, firebase} from '../firebase'

// constantes
const usuarioInicial = {
    username: null,
    uid: null,
    email: null,
    imagen: null,
    activo: false
}

// types
const USUARIO_ACCESO_EXITO = 'USUARIO_ACCESO_EXITO'
const USUARIO_LOGOUT_EXITO = 'USUARIO_LOGOUT_EXITO'

// reducer
export default function usuarioReducer(state = usuarioInicial, action){
    switch(action.type){
        case USUARIO_ACCESO_EXITO:
            return {...state, activo: true, username: action.payload.username, uid: action.payload.uid, email: action.payload.email, imagen: action.payload.imagen}
        case USUARIO_LOGOUT_EXITO:
            return {...usuarioInicial}
        default:
            return {...state}
    }
}

// acciones
export const leerUsuarioAccion = () => async (dispatch) => {
    if(localStorage.getItem('usuario')){
        const usuario = JSON.parse(localStorage.getItem('usuario'))
        dispatch({
            type: USUARIO_ACCESO_EXITO,
            payload: {
                username: usuario.username,
                uid: usuario.uid,
                email: usuario.email,
                imagen: usuario.imagen
            }
        })
    }
}

export const accesoUsuarioAccion = (modo, data) => async(dispatch) => {
    if(modo === 'registro_form'){
        try{
            const res = await auth.createUserWithEmailAndPassword(data.email, data.password)
            await db.collection('usuarios').doc(data.email).set({
                email: data.email,
                imagen: null,
                username: data.usuario,
                uid: res.user.uid
            })
            dispatch({
                type: USUARIO_ACCESO_EXITO,
                payload: {
                    username: data.usuario,
                    uid: res.user.uid,
                    email: data.email,
                    imagen: null
                }
            })
            localStorage.setItem('usuario', JSON.stringify({
                username: data.usuario,
                uid: res.user.uid,
                email: data.email,
                imagen: null
            }))
        }
        catch(error){
            console.log(error)
        }
    }
    if(modo === 'login_form'){
        try{
            await auth.signInWithEmailAndPassword(data.email, data.password)
            const res = await db.collection('usuarios').doc(data.email)
            res.get().then(doc=> {
                dispatch({
                    type: USUARIO_ACCESO_EXITO,
                    payload: {
                        username: doc.data().username,
                        uid: doc.data().uid,
                        email: doc.data().email,
                        imagen: doc.data().imagen
                    }
                })
                localStorage.setItem('usuario', JSON.stringify({
                    username: doc.data().usuario,
                    uid: doc.data().uid,
                    email: doc.data().email,
                    imagen: doc.data().imagen
                }))
            })
            
            
        }
        catch(error){
            console.log(error)
        }
    }
    if(modo === 'google'){
        try{
            const provider = new firebase.auth.GoogleAuthProvider();
            const res = await auth.signInWithPopup(provider)
            await db.collection('usuarios').doc(res.user.email).set({
                username: res.user.displayName,
                uid: res.user.uid,
                email: res.user.email,
                imagen: res.user.photoURL
            })
            dispatch({
                type: USUARIO_ACCESO_EXITO,
                payload: {
                    username: res.user.displayName,
                    uid: res.user.uid,
                    email: res.user.email,
                    imagen: res.user.photoURL
                }
            })
            localStorage.setItem('usuario', JSON.stringify({
                username: res.user.displayName,
                uid: res.user.uid,
                email: res.user.email,
                imagen: res.user.photoURL
            }))
        }
        catch(error){
            console.log(error)
        }
    }
    if(modo === 'facebook'){
        
    }
    if(modo === 'github'){

    }
}

export const logoutUserAccion = () => (dispatch) => {
    auth.signOut();
    dispatch({
        type: USUARIO_LOGOUT_EXITO
    })
    localStorage.removeItem('usuario')
    localStorage.removeItem('tareas')
}