import React from 'react'
import {withRouter} from 'react-router-dom'
import {logoutUserAccion} from '../redux/usuarioDucks'
import {useDispatch} from 'react-redux'

const Top = (props) => {

    const dispatch = useDispatch()

    const logout = () => {
        dispatch(logoutUserAccion())
        props.history.push('/')
    }

    return (
        <div className="top mt-3 col-10 col-md-7 py-3 bg-primary rounded border-bottom border-2">
            <img src={props.imagen ? props.imagen : 'data:image/jpeg;base64,'} alt="Imagen usuario" className="top__imagen"/>
            <p className="top__nombre text-white fs-6 fw-lighter my-0">Bienvenido {props.username}</p>
            <button 
                className="btn btn-transparent px-2 float-end"
                type="button"
                onClick={()=>logout()}
            >
                <i className="fas fa-sign-out-alt text-white"></i>
            </button>
        </div>
    )
}

export default withRouter(Top)
