import React, {useState} from 'react'
import DayPickerInput from 'react-day-picker/DayPickerInput'
import 'react-day-picker/lib/style.css'
import MomentLocaleUtils, {
    formatDate,
    parseDate,
} from 'react-day-picker/moment';  
import 'moment/locale/es';
import shortid from 'shortid';
import Modal from 'react-bootstrap/Modal';
import {useForm, Controller} from 'react-hook-form';
import {useSelector, useDispatch} from 'react-redux';
import {registroTareaAccion, editarTareaAccion, modalAccion} from '../redux/tareasDucks';

const Formulario = (props) => {

    const {register, formState, handleSubmit, control, reset} = useForm({mode: 'onChange', reValidateMode:'onChange'}) 
    const [fecha, setFecha] = useState(new Date())
    const moment = require('moment')
    const dispatch = useDispatch()
    const loading = useSelector( store=> store.tareas.loading)
    const modo = useSelector( store => store.tareas.modo)

    /* Constantes del Modal */
    const modal = useSelector(store => store.tareas.modal)
    const nombre_tarea = useSelector(store => store.tareas.form.nombre)
    const fecha_tarea = useSelector(store => store.tareas.form.fecha)
    const desc_tarea = useSelector(store => store.tareas.form.descripcion)
    const id_tarea = useSelector(store => store.tareas.form.id)

    const onSubmit = data => {
        data.fecha_tarea = moment(fecha).format('ll')
        if(modo === 'agregar'){
            const id = shortid.generate()
            dispatch(registroTareaAccion(data, id, props.email))
        }
        else{
            dispatch(editarTareaAccion(data, id_tarea, props.email))
        }
        reset()
    }
    
    return (
        <div className="col-12 col-md-5 col-lg-4 col-xl-3 text-end">
                        <div className="boton__agregar mt-3 mt-md-0">
                            <button 
                                className="btn btn-primary w-100 btn__agregar--boton"
                                onClick={()=> dispatch(modalAccion(true, ''))}
                            >
                                Nueva tarea
                            </button>
                            <i className="fas fa-plus text-white"></i>
                        </div>
                        
                        <Modal show={modal}>
                                    <Modal.Header>
                                        <Modal.Title>{modo === 'agregar' ? 'Agregar una tarea' : 'Editar una tarea'}</Modal.Title>
                                        <button type="button" className="btn btn-close" onClick={()=>dispatch(modalAccion(false, ''))}></button>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <form className="w-75 mx-auto" onSubmit={handleSubmit(onSubmit)}>
                                            <input 
                                                type="text"
                                                className="form-control mb-3"
                                                name="nombre_tarea"
                                                placeholder="Nombre de la tarea"
                                                defaultValue={nombre_tarea}
                                                ref={register({required:true})}
                                            />
                                            <div className="input-group">
                                                <i className="far fa-calendar-alt"></i>
                                                <Controller
                                                    name='fecha_tarea'
                                                    control={control}
                                                    defaultValue={fecha_tarea}
                                                    as={
                                                        <DayPickerInput 
                                                                formatDate={formatDate}
                                                                parseDate={parseDate}
                                                                onDayChange={day => setFecha(day)}
                                                                format="LL" 
                                                                placeholder={`${formatDate(new Date(), 'LL', 'es')}`}
                                                                dayPickerProps={{
                                                                    locale: 'es',
                                                                    localeUtils: MomentLocaleUtils,
                                                                }}
                                                        />
                                                    }
                                                />
                                            </div>
                                            <textarea 
                                                name="descripcion_tarea"
                                                className="form-control mb-3"
                                                placeholder="Descripción de la tarea"
                                                defaultValue={desc_tarea}
                                                ref={register({required:true, minLength: 10})}
                                            ></textarea>
                                            <div className={`spinner-border text-primary mx-auto mb-3 ${loading ? 'd-block' : 'd-none'}`} role="status">
                                                <span className="visually-hidden">Cargando...</span>
                                            </div>
                                            <button 
                                                className={`btn btn-primary w-100 mb-3 ${loading ? 'd-none' : 'd-block'}`}
                                                type="submit"
                                                disabled={!formState.isValid}
                                            >
                                                {
                                                    modo === 'agregar' ? 'Cargar Tarea': 'Editar Tarea'
                                                }
                                            </button>
                                        </form>
                                    </Modal.Body>
                        </Modal>

                    </div>
    )
}

export default Formulario
