import React from 'react'
import {useDispatch} from 'react-redux'
import {modalAccion, completarTareaAccion, borrarTareaAccion} from '../redux/tareasDucks'

const Tarea = ({tarea, index, email}) => {

    const dispatch = useDispatch()

    const toggleTarea = (e) => {
        if(e.classList.contains('fa-chevron-down')){
            e.classList.replace('fa-chevron-down', 'fa-chevron-up')
            e.closest('.tarea').classList.add('open')
        } 
        else if(e.classList.contains('fa-chevron-up')){
            e.classList.replace('fa-chevron-up', 'fa-chevron-down')
            e.closest('.tarea').classList.remove('open')
        }      
    }

    const editar = (tarea) => {
        dispatch(modalAccion(true, tarea))
    }

    return (
        <li className="tarea py-2 my-2" key={tarea.id} onClick={(e)=>toggleTarea(e.target)}>
            <div className="tarea__contenido">
                <div className="tarea__contenido--top d-block w-100">
                    <h3 className="fs-6 fw-light text-secondary align-left float-md-start">{tarea.nombre}</h3>
                    <div className="float-md-end">
                        <p className="fs-6 fw-lighter text-secondary align-left">{tarea.fecha} <i className="fs-6 fas fa-chevron-down ms-2"></i></p>
                    </div>
                </div>      
                <div className="tarea__contenido--info d-block w-100">
                    <p className="float-md-start fs-6 fw-lighter text-secondary text-start">{tarea.descripcion}</p>
                {
                    tarea.estado === 'activo' ? 
                    <div className="float-md-end tarea__contenido--opciones text-start text-md-end">
                        <button 
                            className="btn btn-outline-primary rounded-pill"
                            onClick={()=> dispatch(completarTareaAccion(tarea.id, email))}
                        >
                            <i className="fas fa-check p-0"></i>
                        </button>
                        <button 
                            className="btn btn-outline-primary rounded-pill ms-2"
                            onClick={()=>editar(tarea)}
                        >
                            <i className="fas fa-pencil-alt p-0"></i>
                        </button>
                        <button 
                            className="btn btn-outline-primary rounded-pill ms-2"
                            onClick={()=> dispatch(borrarTareaAccion(tarea.id, email))}
                        >
                            <i className="fas fa-trash p-0"></i>
                        </button>
                    </div>
                    :
                    null
                }                                                  
                </div>
            </div>
        </li>
    )
}

export default Tarea
