import {db} from '../firebase'

// constantes
const tareaInicial = {
    tareas: [],
    loading: false,
    modal: false,
    form: {},
    modo: null
}

// types
const TAREA_REGISTRO_EXITO = 'TAREA_REGISTRO_EXITO'
const LEER_TAREA_EXITO = 'LEER_TAREA_EXITO'
const LOADING = 'LOADING'
const MODAL = 'MODAL'

// reducer
export default function tareaReducer(state = tareaInicial, action){
    switch(action.type){
        case TAREA_REGISTRO_EXITO :
            return {...state, tareas: [...state.tareas, action.payload]}
        case LEER_TAREA_EXITO :
            return {...state, tareas: action.payload}
        case LOADING :
            return {...state, loading:action.payload}
        case MODAL : 
            return {...state, modal: action.payload.modal, form:{'id': action.payload.form.id, 'nombre': action.payload.form.nombre, 'fecha': action.payload.form.fecha, 'descripcion': action.payload.form.descripcion}, modo:action.payload.modo}
        default :
            return {...tareaInicial}
    }
}

// acciones
export const leerTareaAccion = (email) => async(dispatch) => {
        if(email){
            try{
                const res = await db.collection('usuarios').doc(email).collection('tareas').get()
                const arrayData = res.docs.map(tarea => ({...tarea.data()}))
                dispatch({
                    type: LEER_TAREA_EXITO,
                    payload: arrayData
                })
                localStorage.setItem('tareas',arrayData)
            }
            catch(error){
                console.log(error)
            }
        }
}

export const modalAccion = (accion, tarea) => (dispatch) => {
    let modo = ''
    if(tarea){
        modo = 'editar'
    }
    else{
        modo = 'agregar'
    }
    dispatch({
        type: MODAL,
        payload: {
            modal: accion,
            form: {
                'id': tarea.id,
                'nombre': tarea.nombre,
                'fecha': tarea.fecha,
                'descripcion': tarea.descripcion
            },
            modo: modo
        }
    })
}

export const registroTareaAccion = (data, id, email) => async(dispatch, getState) => {
    const {tareas} = getState().tareas
    dispatch({
        type: LOADING,
        payload: true
    })
    try{
        await db.collection('usuarios').doc(email).collection('tareas').doc(id).set({
            id: id,
            nombre: data.nombre_tarea,
            fecha: data.fecha_tarea,
            descripcion: data.descripcion_tarea,
            estado: 'activo'
        })
        dispatch({
            type: TAREA_REGISTRO_EXITO,
            payload:{
                id: id,
                nombre: data.nombre_tarea,
                fecha: data.fecha_tarea,
                descripcion: data.descripcion_tarea,
                estado: 'activo'
            }
        })
        localStorage.setItem('tareas', JSON.stringify([...tareas, {
            id: id,
            nombre: data.nombre_tarea,
            fecha: data.fecha_tarea,
            descripcion: data.descripcion_tarea,
            estado: 'activo'
        }]))
        dispatch({
            type: LOADING,
            payload: false
        })
        dispatch({
            type: MODAL,
            payload:{
                modal: false,
                form: {},
                modo: null
            }
        })
    }
    catch(error){
        console.log(error)
    }
}

export const completarTareaAccion = (id, email) => async(dispatch) => {
    try{
        await db.collection('usuarios').doc(email).collection('tareas').doc(id).update({estado:'completada'})
        const tareas = await db.collection('usuarios').doc(email).collection('tareas').get()
        const arrayData = tareas.docs.map(tarea => ({...tarea.data()}))
        dispatch({
            type: LEER_TAREA_EXITO,
            payload: arrayData
        })
        localStorage.setItem('tareas', JSON.stringify(arrayData))
    }
    catch(error){
        console.log(error)
    }
}

export const borrarTareaAccion = (id, email) => async(dispatch) => {
    try{
        await db.collection('usuarios').doc(email).collection('tareas').doc(id).delete()
        const tareas = await db.collection('usuarios').doc(email).collection('tareas').get()
        const arrayData = tareas.docs.map(tarea => ({...tarea.data()}))
        dispatch({
            type: LEER_TAREA_EXITO,
            payload: arrayData
        })
        localStorage.setItem('tareas', JSON.stringify(arrayData))
    }
    catch(error){
        console.log(error)
    }
}

export const editarTareaAccion = (data, id, email) => async(dispatch) => {
    dispatch({
        type: LOADING,
        payload: true
    })
    try{    
        await db.collection('usuarios').doc(email).collection('tareas').doc(id).update({
            nombre: data.nombre_tarea,
            fecha: data.fecha_tarea,
            descripcion: data.descripcion_tarea,
            estado: 'activo'
        })
        const res = await db.collection('usuarios').doc(email).collection('tareas').get()
        const arrayData = res.docs.map(tarea => ({...tarea.data()}))
        dispatch({
            type: LEER_TAREA_EXITO,
            payload: arrayData
        })
        dispatch({
            type: LOADING,
            payload: false
        })
        dispatch({
            type: MODAL,
            payload: {
                modal: false,
                form: {},
                modo: null
            }
        })
    }
    catch(error){
        console.log(error)
    }
}