import {createStore, combineReducers, compose, applyMiddleware} from 'redux'
import thunk from 'redux-thunk'
import {leerUsuarioAccion} from '../redux/usuarioDucks'
import {leerTareaAccion} from '../redux/tareasDucks'

import usuarioReducer from '../redux/usuarioDucks'
import tareaReducer from '../redux/tareasDucks'

const rootReducer = combineReducers({
    usuario : usuarioReducer,
    tareas: tareaReducer
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

export default function generateStore(){
    const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)))
    leerUsuarioAccion()(store.dispatch)
    leerTareaAccion()(store.dispatch)
    return store;
}