import React, {useState, useEffect} from 'react'
import Tarea from './Tarea'
const Tareas = (props) => {

    
    const [estado, setEstado] = useState('activo')
    
    useEffect(()=> {
        const filtraTareas = (estado) =>{
            const arrayFiltrado = props.tareas.filter(tarea => tarea.estado === estado)
            props.setTareasFiltradas(arrayFiltrado)
        }
        filtraTareas('activo')
    },[props.tareas, props.setTareasFiltradas])

    const filtrarTareas = (estado) => {
        const arrayFiltrado = props.tareas.filter(tarea => tarea.estado === estado)
              props.setTareasFiltradas(arrayFiltrado)
              setEstado(estado)
    }


    return (
        <div className="tareas col-10 col-md-7 bg-white rounded border-bottom border-4 p-b-3 px-0">
                    <div className="tabs">
                        <div 
                            className={`tabs__tab ${estado === 'activo' ? 'active' : null}`}
                            onClick={()=> filtrarTareas('activo')}
                        >En Desarrollo</div>
                        <div 
                            className={`tabs__tab ${estado === 'completada' ? 'active' : null}`}
                            onClick={()=> filtrarTareas('completada')}
                        >Completadas</div>
                    </div>
                    <div className="listado">
                        <ul className="listado__tareas">
                            {
                                props.tareasFiltradas && props.tareasFiltradas.length > 0 ? 
                                
                                props.filtro.map((tarea) => (
                                    <Tarea key={tarea.id} tarea={tarea} email={props.email}/>        
                                ))
                                
                                :
                                <p className="text-center my-3 fs-6 fw-light text-secondary">No hay tareas cargadas.</p>
                            }
                            
                        </ul>
                    </div>
                </div>
    )
}

export default Tareas
