import React, {useEffect, useState} from 'react'
import Top from '../components/Top'
import Buscador from '../components/Buscador'
import Formulario from '../components/Formulario'
import Tareas from '../components/Tareas'
import {auth} from '../firebase'
import {withRouter} from 'react-router-dom'
import {useSelector, useDispatch} from 'react-redux'
import { leerTareaAccion } from '../redux/tareasDucks'

const Inicio = (props) => {

    const dispatch = useDispatch()
    const username = useSelector(store => store.usuario.username)
    const imagen = useSelector(store => store.usuario.imagen)
    const email = useSelector(store => store.usuario.email)
    const tareas = useSelector(store => store.tareas.tareas)
    const [tareasFiltradas, setTareasFiltradas] = useState([])
    const [busqueda, setBusqueda] = useState('')

    useEffect(()=>{
        if(auth.currentUser){
            dispatch(leerTareaAccion(email))
        }
        else{
            props.history.push('/')
        }
    },[props.history, dispatch, email])

    const busquedaDinamica = () => {
        return tareasFiltradas.filter(tarea => tarea.nombre.toLowerCase().includes(busqueda.toLowerCase()))
    }

    return (
        <div className="inicio min-vh-100">
            <div className="row justify-content-center">
                <Top username={username} imagen={imagen}/>
            </div>
            
            <div className="row justify-content-center my-3">
                <div className="col-10 col-md-7 p-0">
                    <div className="row justify-content-between p-0">
                    <Buscador busqueda={busqueda} setBusqueda={setBusqueda} tareasFiltradas={tareasFiltradas}/>
                    <Formulario email={email}/>
                    </div>
                </div>
            </div>

            <div className="row justify-content-center">
                <Tareas tareas={tareas} tareasFiltradas={tareasFiltradas} setTareasFiltradas={setTareasFiltradas} email={email} filtro={busquedaDinamica()}/> 
            </div>
        </div>
    )
}

export default withRouter(Inicio)
